.choicesContainer {
  display: flex;
  width: 100%;
  table-layout: fixed;
  margin-bottom: 16px;
  justify-content: space-between;

  .choice {
    padding: 8px;
    width: 100%;
    margin: 1px;
    font-weight: bold;
    font-size: larger;
    border: 1px solid;
    text-align: center;
  }
}

.choiceLabels{
  margin-top: 16px;
  display: flex;
  justify-content: space-between;
}