@import '../../styles/variables' ;

.content{
  width: 100%;
  margin: 0;
  letter-spacing: 0.1px;
  word-break: break-word;

  &.question {
    margin-bottom: 4px;
  }

  .required {
    margin-left: 5px;
  }
}

.header{
  margin-bottom: 12px;
}

.textDescription {
  max-width: 100%;
  margin-bottom: 4px;
}

.groupQuestion {
  margin-bottom: 12px;
  padding: 24px;
  border-style: solid;
  border-width: 1px;
  border-radius: 8px;
  word-wrap: break-word;
  page-break-inside: avoid;
  transition: all 200ms cubic-bezier(0, 0, 0.2, 1);
}
