.imageGrid {
  margin-top: 16px;
}

.imageContainer {
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}

.rankContainer {
  display: table;
  text-align: center;
  vertical-align: middle;
  border: 1px solid transparent;
  background-color: rgba(255, 255, 255, 0.7);
}

.rankValue {
  color: white;
  display: table-cell;
  vertical-align: middle;
}