@import '../../styles/variables.scss' ;

.surveyContent {
    margin: 10px 30px ;
}

.surveyGroups {
  margin: auto;
  max-width: 800px;
  min-width: 300px;
}

.cardImage {
  max-height: 22.5vw;
  height: 160px;
  margin: 12px auto;
  background-size: cover;
  background-position: center;
  border: 1px solid ;
  border-radius: 8px;
}

.surveyEmpty{
  position: fixed;
    top: 33%;
    width: 100%;
    text-align: center;
    font-size: 64px;
    color: red;
}

.surveyPage{
  max-height: 100vh;
  overflow-y: scroll;
}
